<template>
<div>
	<nav-bar></nav-bar>
	<h1>
		{{title}}
	</h1>
</div>
</template>
<script>
import router from '../router'
import NavBar from './NavBar'

export default {
	name: 'ProjectDetail',
	components: {
		'nav-bar': NavBar
	},
	data() {
		return {
			title: router.currentRoute.path
		}
	},
	methods: {

	},
	created: function() {
		// console.log("router ", router.currentRoute)
	}
};
</script>
<style scoped>
h1, h2, h4, h5 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}
body, html {
	margin: 0px;
	padding: 0;
}
</style>


